angular.module('picafarm', ['ui.router']).config(function ($stateProvider, $urlRouterProvider, $locationProvider) {
    $stateProvider.state('home', {
        url: '/',
        template: '<farmer-search></farmer-search>'
    }).state('login', {
        url: '/login',
        template: '<login></login>'
    });

    $urlRouterProvider.otherwise('/');
});
'use strict';

angular.module('picafarm').controller('mainCtrl', function ($scope, $http) {

  $scope.farmerArr = [];

  $scope.get = function () {
    $http.get("/search").then(function (response) {
      console.log(response.data);
      $scope.farmerArr = response.data;
    }).catch(function (response) {
      console.log(response);
    });
  };

  $scope.get();
});
